import React, { useEffect, useState } from "react";
import {
  CCard,
  CCol,
  CRow,
  CCardHeader,
  CCardBody,
  CButton,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CForm,
  CFormGroup,
  CLabel,
  CInput,
  CFormText,
  CSelect,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import MaterialTable from "material-table";
import { useDispatch, useSelector } from "react-redux";
import {
  setFormVariabel,
  SetJenisVariabel,
  PostVariabel,
  UpdateVariabel,
  GetDataVariabel,
  setVariabelId,
  DeleteVariabel,
} from "src/common/redux/action";

const Variabel = () => {
  const [stateUpdate, setStateUpdate] = useState(false);
  const { dataComboJenisVariabel } = useSelector(
    (state) => state.dropdownReducer
  );
  const { modalVariabel, formVariabel, GetVariabels } = useSelector(
    (state) => state.masterVariabelReducer
  );

   const {
     IdUser,
     IdJenisVariabel,
     IdVariabel,
     Nama,
     Presentase,
     JenisVariabel,
   } = formVariabel;
   const dispatch = useDispatch();

   const getJenisVariabels = () =>
     dispatch(SetJenisVariabel("DATA_CMB_JENIS_VARIABEL"));

    const getVariabels = () => dispatch(GetDataVariabel("GET_VARIABELS"));



  const prosesVariabelAdd = () => dispatch(PostVariabel(formVariabel));
  const prosesVariabelEdit = () => dispatch(UpdateVariabel(formVariabel))

  useEffect(()=>{
    getJenisVariabels()
    getVariabels();
  },[dispatch]);

  const showModal = () =>
    dispatch({ type: "MODAL_VARIABEL", payload: !modalVariabel });

  const handleModalVariabel = (type, idVariabel) =>{
     if (type == "add_variabel") {
       showModal();
      setStateUpdate(false);
      dispatch({ type: "RESET_FORM_VARIABEL" });
     } else {
       showModal();
       setStateUpdate(true);
       dispatch(setVariabelId(idVariabel));
     }
  }

  /**
   * handle proses delet
   */
  const prosesDelete = (idVariabel, nama) =>
    dispatch(DeleteVariabel(idVariabel, nama));

  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row d-flex justify-content-center">
            <button
              type="button"
              className="btn btn-sm btn-dark text-center mr-2"
              onClick={() => handleModalVariabel("edit", rowData.idVariabel)}
            >
              <CIcon name="cil-pencil" /> Update
            </button>

            <button
              type="button"
              className="btn btn-sm btn-danger"
              onClick={() => prosesDelete(rowData.idVariabel, rowData.nama)}
            >
              <CIcon name="cil-drop" /> Hapus
            </button>
          </div>
        );
      },
    },
    { title: "Jenis Variabel", field: "jenisVariabel" },
    { title: "Nama Variabel", field: "nama" },
    { title: "Presentase", field: "presentase" },
  ];

  return (
    <div>
      <CRow>
        <CCol md="12" xl="12" xs="12">
          <CCard>
            <CCardHeader>
              <CCol md="3">
                <CButton
                  block
                  color="primary"
                  onClick={() => handleModalVariabel("add_variabel")}
                >
                  <FontAwesomeIcon icon="plus" />
                  Tambah
                </CButton>
              </CCol>
            </CCardHeader>
            <CCardBody>
              <MaterialTable
                title=""
                options={{
                  headerStyle: {
                    backgroundColor: "#01579b",
                    color: "#FFF",
                  },
                  filtering: true,
                }}
                columns={columns}
                data={GetVariabels}
              />
            </CCardBody>
          </CCard>
        </CCol>

        <CModal show={modalVariabel} onClose={showModal} size="lg">
          <CModalHeader closeButton>
            <CModalTitle>Form Variabel</CModalTitle>
          </CModalHeader>
          <CModalBody>
            <CForm>
              <CFormGroup>
                <CLabel htmlFor="nf-nama">Nama</CLabel>
                <CInput
                  type="text"
                  id="nf-kode"
                  name="nama"
                  placeholder="input nama variabel ..."
                  value={Nama}
                  onChange={(e) =>
                    dispatch(setFormVariabel("Nama", e.target.value))
                  }
                />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="nf-nama"> Jenis </CLabel>
                <CSelect
                  value={IdJenisVariabel}
                  onChange={(e) =>
                    dispatch(setFormVariabel("IdJenisVariabel", e.target.value))
                  }
                >
                  <option value="0">-- Pilih Jenis Variabel --</option>
                  {dataComboJenisVariabel.map((val) => (
                    <option
                      key={val.idJenisVariabel}
                      value={val.idJenisVariabel}
                    >
                      {val.nama}
                    </option>
                  ))}
                </CSelect>
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="nf-nama"> Presentase </CLabel>
                <CInput
                  type="number"
                  value={Presentase}
                  onChange={(e) =>
                    dispatch(setFormVariabel("Presentase", e.target.value))
                  }
                ></CInput>
              </CFormGroup>
            </CForm>
          </CModalBody>
          <CModalFooter>
            <CButton color="primary" onClick={stateUpdate ? prosesVariabelEdit: prosesVariabelAdd}>
              {
                stateUpdate ? 'Update' : 'Tambah'
              }
            </CButton>
            <CButton color="secondary" onClick={showModal}>
              Cancel
            </CButton>
          </CModalFooter>
        </CModal>
      </CRow>
    </div>
  );
};

export default Variabel;
